<script>
    export default{
        data(){
            return {
                show:false
            }
        },
        methods:{
             modal(){
                const h = this.$createElement;
                this.$modal({
                    title:'',
                    cancleBtn:'',
                    confirmBtn:'',
                    message:h('p', null, [
                        h('span', null, ' '),
                        h('i', { style: 'color: teal' }, 'VNode')
                    ])
                }).then(action=>{
                    console.log(action)
                }).catch(action=>{
                    console.log(action)
                })
            },
            alert(){
                this.$alert({
                    message:'alert'
                }).then(action=>{
                    console.log(action)
                }).catch(action=>{
                    console.log(action)
                })
            },
            prompt(){
                this.$prompt({
                    message:'prompt'
                }).then(action=>{
                    console.log(action)
                }).catch(action=>{
                    console.log(action)
                })
            },
            confirm(){
                this.$confirm({
                    message:'confirm'
                }).then(action=>{
                    console.log(action)
                }).catch(action=>{
                    console.log(action)
                })
            },
            info(){
                this.$modal({
                    level:'info',
                    message:'info'
                })
            },
            success(){
                this.$modal({
                    level:'success',
                    message:'success'
                })
            },
            warning(){
                this.$modal({
                    level:'warning',
                    message:'warning'
                })
            },
            question(){
                this.$modal({
                    level:'question',
                    message:'question'
                })
            },
            danger(){
                this.$modal({
                    level:'danger',
                    message:'danger'
                })
            }
        }
    }
</script>


